@import '../vars.scss';

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Athelas-Bold';
    font-weight: bold;
}
body{
    max-width: 1280px;
    margin: 0 auto;
}

.paddings {
    padding: 0 55px;
}
.mt100{
    margin-top: 100px;
}
.bg_beige{
    background-color: $beige;
}

h1 {
    font-size: 44px;
    text-transform: uppercase;
}

h2 {
    font-size: 44px;
    text-transform: uppercase;
}

h3 {
    font-size: 30px;
}

h4 {
    font-size: 22px;
}

p {
    color: $black;
    font-family: 'Montserrat';
}

.fz40 {
    font-size: 2.5rem;
}

.fz30 {
    font-size: 1.875rem;
}

.fz20 {
    font-size: 1rem;
}

.fz16 {
    font-size: 1rem;
}

.fz14 {
    font-size: 0.875rem;
}

.red-marks{
    font-size: 34px;
    font-weight: 500;
    color: $red;
}

.title__block {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;

    h1 {
        text-align: center;
    }

    .subtitle {
        position: relative;
        color: $red;
        text-align: center;
        text-transform: uppercase;
        font-size: 14px;

        &::after {
            content: '';
            position: absolute;
            left: -40px;
            top: 7px;
            width: 25px;
            height: 1px;
            background-color: $red;
        }

        &::before {
            content: '';
            position: absolute;
            right: -40px;
            top: 7px;
            width: 25px;
            height: 1px;
            background-color: $red;
        }
    }
}

.button-red-border {
    a {
        display: inline-block;
        padding: 20px;
        border: 2px solid $red;
        color: $black;
    }
}

.swiper {
    width: 100% !important;
}

.slider-buttons {
    display: flex;
    gap: 15px;

    .swiper-button-next:after, .swiper-rtl .swiper-button-prev:after{
        content: url('../../img/Button.png') !important;
        width: 100%;
        height: 100%;
    }
    .swiper-button-prev:after, .swiper-rtl .swiper-button-next:after{
        content: url('../../img/Button.png') !important;
        transform: rotate(180deg);
        width: 100%;
        height: 100%;
    }

    div {
        margin-top: 0;
        position: static !important;
        color: $red;
    }
}

.underslider-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title-with-slider-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-with-red-point {
    display: flex;
    flex-direction: column;
    gap: 15px;

    li {
        position: relative;
        margin-left: 15px;

        &::after {
            content: '';
            position: absolute;
            left: -15px;
            top: 5px;
            background-color: $red;
            height: 5px;
            width: 5px;
            border-radius: 5px;

        }
    }
}