body {
    background: $bg_body;
}


.quote__block__wrapper {
    background: $beige;
    padding: 50px 0;

    .quote__block {
        display: block;
        max-width: 1000px;
        margin: 0 auto;

        .flex {
            gap: 20px;
            margin-top: 20px;
        }

        .video {
            flex-basis: 40%;
            width: 100%;
            height: 250px;
        }

        .text {
            flex-basis: 50%;
            font-size: 38px;
        }
    }
}

.text-with-image-block {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .content {
        flex-basis: 50%;
        display: flex;
        flex-direction: column;
        gap: 50px !important;
        padding: 40px;
        padding-right: 80px;

        .text {
            display: flex;
            flex-direction: column;
            gap: 30px;
        }

        .text2 {
            display: flex;
            flex-direction: column;
            gap: 15px;
        }
    }

    .image {
        flex-basis: 50%;
        display: block;
        flex: 1 1 50%;

    }
}

.staff-slider {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .staffSlider {
        width: 100%;
    }
}

.products__wrapper {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.founder__block {
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .content {
        flex-basis: 65%;
        padding: 40px;

        h2 {
            margin-bottom: 40px;
        }

        .line {
            background-color: $red;
            height: 1px;
            width: 100px;
            margin: 15px 0;
        }

        .founder__about {
            opacity: .7;
        }

        .text {
            display: flex;
            flex-direction: column;
            gap: 20px;
            margin-top: 30px;
        }
    }

    .image {
        flex-basis: 35%;
    }
}

.equipment__block {
    margin-top: 100px;

    .title-with-slider-button {
        margin-bottom: 30px;
    }

    .equipmentSlider {
        .swiper-slide {
            background-color: $beige;
            padding: 40px;
            display: flex;

            .content {
                flex-basis: 50%;
                display: flex;
                flex-direction: column;
                gap: 20px;

            }

            .image {
                margin-bottom: -40px;
            }
        }
    }
}

.about-salon {
    margin-top: 100px;

    h2 {
        margin-bottom: 30px;
    }

    .grid-block {
        margin-bottom: 30px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-gap: 30px;

        .item {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
    }

    .image__block {
        width: 100%;
    }
}

.results__block {
    margin-top: 100px;

    .title-with-slider-button {
        margin-bottom: 30px;
    }

    .resultsSlider {
        .swiper-slide {
            height: 300px;
        }
    }
}

.feedbacks-slider {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .feedbacksSlider {
        .swiper-slide {
            min-height: 400px;
            padding: 20px;
            background-color: $beige;
            display: flex;
            flex-direction: column;
            gap: 20px;

            p {
                flex: 1;
                line-height: 135%;
            }

            .name {
                font-family: 'Athelas-Bold';
                font-weight: bold;
                font-size: 24px;
            }
        }
    }
}

.map__block {

    margin-top: 100px;

    h2 {
        margin-bottom: 30px;
    }

    .map-with-block {
        width: 100%;
        background-image: url(../img/map.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        .info__block__wrapper {
            width: 80%;
            margin: 0 auto;
            padding: 100px 0;
            display: flex;
            align-items: center;
            justify-content: end;

            .info__block {
                background-color: $bg_body;
                z-index: 999;
                height: 400px;
                width: 350px;
            }
        }
    }
}

.about_doctor__block {
    margin: 0 40px;
    margin-top: 100px;
    display: grid;
    grid-template-columns: calc(65% - 2.5px) calc(35% - 2.5px);
    gap: 5px;

    &-timetable {
        padding: 40px;
        background-color: $beige;

        .timetable__block {
            width: 100%;
            margin-bottom: 30px;
            border: 2px solid $bg_body;

            h4 {
                padding: 20px;
            }

            .timetable {
                .item {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 8px 20px;

                    &:nth-child(odd) {
                        background-color: $bg_body;
                    }
                }
            }
        }
    }

    &-info {
        padding: 40px;
        background-color: $beige;

        .doctor {
            display: flex;
            align-items: center;
            gap: 25px;

            .image {
                height: 300px;
                width: 300px;
            }

            .name {
                font-size: 44px;
                max-width: 400px;
            }
        }

        .tabs {
            margin-top: 50px;

            .tabs {
                overflow: hidden;
                border-radius: 15px;
            }

            .tabs__head {
                display: flex;
                align-items: center;
                gap: 30px;
                flex-wrap: wrap;
                position: relative;

                &::before {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 2px;
                    background-color: $bg_body;
                }
            }

            .tabs__caption {
                padding: 10px;
                padding-left: 0;
                padding-right: 30px;
                font-size: 18px;
                cursor: pointer;
                -webkit-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
                transition: 0.25s;
                z-index: 999;
            }

            .tabs__caption:hover {
                opacity: 0.8;
            }

            .tabs__caption_active {
                color: $red;
                border-bottom: 2px solid $red;
            }

            .tabs__body {
                position: relative;
                top: -2px;
                transition: 0.2s;
            }

            .tabs__content {
                display: none;
                padding: 30px 0;
                transform: scaleY(0.8);
                opacity: 0;
                transform-origin: center top;
                -webkit-animation: showContent 0.1s forwards;
                animation: showContent 0.1s forwards;
                flex-direction: column;
                gap: 20px;

                h4 {
                    margin-bottom: 10px;
                }

                .flex {
                    gap: 50px;

                    .item {
                        max-width: 25%;
                    }
                }
            }

            .tabs__content_active {
                display: flex;
            }

            @-webkit-keyframes showContent {
                0% {
                    transform: scaleY(0.8);
                    opacity: 0;
                }

                100% {
                    transform: scaleY(1);
                    opacity: 1;
                }
            }

            @keyframes showContent {
                0% {
                    transform: scaleY(0.8);
                    opacity: 0;
                }

                100% {
                    transform: scaleY(1);
                    opacity: 1;
                }
            }
        }

    }
}

.price__block {
    margin-top: 100px;
    padding: 0 55px;
    display: flex;
    gap: 30px;

    .sidebar {
        background-color: $beige;
        flex-basis: 30%;
        padding: 30px;

        ul {
            display: flex;
            flex-direction: column;
            gap: 8px;

            li {
                position: relative;
                width: 100%;

                a {
                    font-size: 16px;
                    color: $black;
                    font-weight: 400;
                }
            }

            .active {
                a {
                    color: $red;
                }

                &::before {
                    content: '';
                    position: absolute;
                    right: -30px;
                    top: 0;
                    height: 100%;
                    width: 1px;
                    background-color: $red;
                }
            }
        }
    }

    &-info {
        flex-basis: 70%;
        display: flex;
        flex-direction: column;
        gap: 40px;

        .search {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: $beige;
            padding: 22px 30px;

            input {
                background-color: transparent;
                border: none;
                outline: none;
                width: 100%;
                font-size: 20px;

                &::placeholder {
                    font-size: 20px;
                    color: #8D8D8D;
                }
            }
        }

        .price_list {
            display: flex;
            flex-direction: column;
            gap: 40px;

            ul {
                margin-top: 15px;
                .title {
                    padding: 20px 30px;
                    padding-bottom: 14px;

                    span {
                        font-size: 22px;
                        font-weight: bold;
                        font-family: 'Athelas-Bold';
                    }
                }

                li {
                    background-color: $bg_body;
                    padding: 10px 30px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    &:nth-child(odd) {
                        background-color: $beige;
                    }

                    span {
                        font-size: 16px;
                        font-weight: 400;
                        color: $black;
                    }
                }
            }
        }
    }
}

.certificate__grid{
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
}

.feedback__grid{
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;

    .item{
        min-height: 400px;
        background-color: $beige;
        padding: 30px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        p{
            flex: 1;
        }
        .flex{
            .name{
                font-size: 22px;
                font-weight: 700;
                font-family: 'Athelas-Bold';
            }
            .magnifire{
                flex-basis: 40%;
                display: flex;
                justify-content: end;
            }
        }
    }
}

.form__block{
   margin-top: 100px;
   background-color: $beige;
   .content{
    
    form{
        display: flex;
        flex-direction: column;
        gap: 20px;
        input{
            padding: 20px;
            width: 100%;
            background-color: transparent;
            border: 1px solid $black;
            outline: none;
            font-size: 16px;
            &::placeholder{
                color: #8D8D8D;
                font-size: 16px;
            }
        }
        textarea{
            padding: 20px;
            resize: none;
            width: 100%;
            background-color: transparent;
            border: 1px solid $black;
            outline: none;
            font-size: 16px;
            &::placeholder{
                color: #8D8D8D;
                font-size: 16px;
            }
        }
    }
   } 
}